import React, { ChangeEvent, useState } from "react";
import axios from "axios";
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import 'bootstrap/dist/css/bootstrap.min.css';
import { useNavigate } from 'react-router-dom';

function callback() {
    throw new Error("Function not implemented.");
}

const Login = (): JSX.Element => {

    const navigate = useNavigate();
    const [id, setId] = useState<string>();
    const [pass, setPass] = useState<string>();

    const updateUserId = (event: ChangeEvent<HTMLInputElement>) => {
        const inputId = event.currentTarget.value;
        setId(inputId);
    };

    const updateUserPass = (event: ChangeEvent<HTMLInputElement>) => {
        const inputPass = event.currentTarget.value;
        setPass(inputPass);
    };


    const addUser = async () => {
        try {
            await axios.post("http://localhost:5000/user/login", { id, pass });
            await callback();
            navigate("/Main/Main");
        }catch (e) {
            if (axios.isAxiosError(e) && e.response) {
              const { data } = e.response;
              if (data) {
                alert(data.message);
              }
            }
          }
    };

    
      return (
        <>
        <Form>
          <Form.Group className="mb-3" controlId="formBasicId"onChange={updateUserId}>
            <Form.Label>ID</Form.Label>
            <Form.Control type="email" placeholder="Enter Id" />

          </Form.Group>
          <Form.Group className="mb-3" controlId="formBasicPassword" onChange={updateUserPass}>
            <Form.Label>Password</Form.Label>
            <Form.Control type="password" placeholder="Password" />
          </Form.Group>
          <Form.Group className="mb-3" controlId="formBasicCheckbox">
            <Form.Check type="checkbox" label="Check me out" />
          </Form.Group>
          <Button variant="primary" type="submit" onClick={addUser}>
            Sign in
          </Button>
          &nbsp;
          <Button variant="primary" onClick={()=>{navigate("/SignUp")}}>
            Sign up
          </Button>
        </Form>
        </>
      );
};

export default Login;


